import {Injectable} from '@angular/core';
import {environment} from '../environments/environment';
import {Observable, throwError} from 'rxjs';
import {HttpClient, HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import {catchError, map, tap} from 'rxjs/operators';
import {Router} from '@angular/router';


declare var PNotify: any;
declare var $: any;

@Injectable()
export class  HttpHelperService {
  constructor (
              private http: HttpClient,
              public myRoute: Router,
              ) {

  }
  post(url, postBody: any, page?, loader = true) {
    var d = new Date();
    var timeZone = d.getTimezoneOffset();
    const token = '';
    let queryString =  '';
    if (page) {
      queryString = '';
    }
    console.log("call post api");
    if (loader)
      this.beforeRequest();
    const httpOptions = {
      headers: new HttpHeaders({
        'Access-Control-Allow-Origin': '*',
        'Accept': 'application/json'
      })
    };

    let response;
    if (token)
      response = this.http.post<any>(this.getFullUrl(url + queryString), postBody, httpOptions);
    else
      response = this.http.post<any>(this.getFullUrl(url), postBody, httpOptions);
    console.log("full url from helper",this.getFullUrl(url + queryString));
    return response.pipe(
      map(data => data),
      tap(data => this.onFinally(), error => this.onError(error)),
      catchError(this.onCatch)
    );
  }

  get(url) {
    document.getElementById('spinner').style.display = 'block';
  //  const token = this.auth.getBearerToken();
    const httpOptions = {
      headers: new HttpHeaders({
        'Access-Control-Allow-Origin': '*',
        'Accept': 'application/json'
      })
    };
    let response;
    response=this.http.get<any>(this.getFullUrl(url), httpOptions);
    return response.pipe(
      map(data => data),
      tap(data => this.onFinally(), error => this.onError(error)),
      catchError(this.onCatch)
    );
    
  }

  upload(url, postBody: any) {
    let formData: FormData = new FormData();

    for (let property in postBody) {
      if (postBody[property] instanceof Array) {
        for (var files in postBody[property]) {
          formData.append(property + '[]', postBody[property][files]);
        }
      }
      else
        formData.append(property, postBody[property]);

    }

    return this.post(url, formData);

  }


  getQueryParam(url, name) {
    var q = url.match(new RegExp('[?&]' + name + '=([^&#]*)'));
    return q && q[1];
  }

  public getFullUrl(url: string): string {
     return environment.apiEndpoint + url;
  }

  /**
   * Before any Request.
   */
  private beforeRequest(): void {
   // this.notifyService.showPreloader();
  }

  /**
   * After any request.
   */
  private afterRequest(): void {
 //   this.notifyService.hidePreloader();
  }

  private onCatch(error: HttpErrorResponse): Observable<any> {
    console.log("error oncatch",error)
    if (error.error.message != 'Not Found')
      new PNotify({
        title: '',
        text: 'Internal Error Occured. Try After Some Time!!.',
        type: 'error',
        styling: 'bootstrap3',
        delay: 1500
      });
    return throwError(error);
  }


  private onSuccess(res: Response): void {
  }


  private onError(error: any): void {
    console.log("call onerror",error);
    this.afterRequest();
    // this.notifyService.popError();
    if (error.error.message == 'Expired token') {
  //    this.auth.logout();
      localStorage.removeItem('LoggedInUser');
      sessionStorage.removeItem('LoggedInUser');
      localStorage.removeItem('user');
      this.myRoute.navigate(['/login']);

    }
    if (error.error.message == 'You are not authorized to access that location.') {
    //  this.auth.logout();
      this.myRoute.navigate(['/login']);
    }
    if (error.error.message == 'Site is under maintenance.') {

      localStorage.removeItem('LoggedInUser');
      sessionStorage.removeItem('LoggedInUser');
      localStorage.removeItem('user');

      console.log('error.error.message', error.error.message);
      this.myRoute.navigateByUrl('/under-construction');
    }
  }

  /**
   * onFinally
   */
  private onFinally(): void {
    this.afterRequest();
  }

  public showSucess(success) {
    new PNotify({
      title: '',
      text: success,
      type: 'success',
      styling: 'bootstrap3',
      delay: 3000
    });
  }

  public showError(error) {
    new PNotify({
      title: '',
      text: error,
      type: 'error',
      styling: 'bootstrap3',
      delay: 3000
    });
  }




}
